.weddingVenueContainer {
    text-align: right;
    margin-bottom: 20px;
  }
  
  .weddingVenueText  {
    margin: 5px 5px;
    font-size: 1rem;
    font-weight: 500;
    color: #333;
  }

  .operationButtonContainer {
    display: flex;
    justify-content: flex-end;
    gap: 16px;
  }

  .initButton {
    background-color: #ffffff;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 8px 16px;
    margin-right: 8px;
    font-size: 14px;
    cursor: pointer;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
    margin-right: 0;
  }
  
  .initButton:hover {
    background-color: #f2f2f2;
  }
  
  .lockButton {
    background-color: #ffffff;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 8px 16px;
    font-size: 14px;
    cursor: pointer;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .lockButton:hover {
    background-color: #f2f2f2;
  }

  .mapLockMessage {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    text-align: center;
    padding: 12px 16px;
    font-size: 14px;
    line-height: 1.5;
    z-index: 10;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    pointer-events: none;
  }
  
  .mapContainer {
    width: 100%;
    aspect-ratio: 1 / 1; /* 정사각형 비율 */
    margin: 20px auto;
  }

  .directionsContainer {
    text-align: left;
    margin-bottom: 20px;
  }
  
  .addressHeader, .subwayHeader, .busHeader, .addressHeader, .parkingHeader {
    color: #333;
    font-size: 1rem;
    font-weight: bold;
    margin-top: 30px;
  }

  .address, .subwayText, .busText, .addressText, .parkingText {
    color: #333;
    font-size: 0.9rem;
    font-weight: normal;
    white-space: pre-line; /* 줄바꿈 처리 */
  }

/* 전체 컨테이너 스타일 */
.mapButtons {
  display: flex;
  justify-content: space-evenly; /* 버튼 간 간격 균등하게 분배 */
  align-items: center; /* 수직 정렬 유지 */
  background-color: transparent; /* 배경 투명 */
  margin-top: 40px;
}

/* 버튼 스타일 */
.mapButtons button {
  border: none; /* 테두리 제거 */
  border-radius: 5px; /* 모서리 둥글게 */
  cursor: pointer; /* 포인터 커서 */
  background: none; /* 배경 제거 */
  padding: 0; /* 불필요한 여백 제거 */
}

/* 이미지 스타일 */
.mapButtons button img {
  width: 60px; /* 이미지 너비 */
  height: auto; /* 이미지 비율 유지 */
  display: block; /* 이미지의 여백 제거 */
  border: none; /* 이미지 외곽선 제거 */
  box-shadow: none; /* 이미지 그림자 제거 */
  transition: transform 0.3s ease; /* 호버 시 애니메이션 */
}

/* 버튼 호버 효과 */
.mapButtons button:hover img {
  transform: scale(1.1); /* 호버 시 이미지 확대 */
}

/* 로고 명 */
.mapButtons button h4 {
  margin: 10px 0 0; /* 이미지와 텍스트 간 간격 */
  font-size: 12px; /* 적절한 텍스트 크기 */
  color: #333; /* 텍스트 색상 */
  font-weight: normal; /* 텍스트 굵기 */
  text-align: center; /* 텍스트 중앙 정렬 */
}

  